import React, { useState, useEffect } from 'react';
import { useHistory } from 'react-router-dom';
import Main from '../main/main';
import './details.css';
import Youtube from 'react-youtube';
import useLaunches from '../useLaunches/useLaunches';

const Details = ({match}) => {

	const [launch, setLaunch] = useState(null);

	const { getLaunch } = useLaunches();

	let id = match.params.id;

	useEffect(() => {
		setLaunch(getLaunch(id))
	}, [getLaunch, id]);

	console.log(launch);
	
	const history = useHistory();
	
	return (
	<>
	<Main name={launch?.name}/>
    <main className="details">
		<div className="container">
			<div className="details-row">
				<div className="details-image">
					<img src={launch?.links.patch.small} alt={launch?.name}/>
				</div>
				<div className="details-content">
					<p className="details-description">{launch?.details}</p>
				</div>
			</div>
			<div>
				{//<iframe title="unique" className="details-youtube" width="560" height="315" src="https://www.youtube.com/embed/dLQ2tZEH6G0" frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
				}
				<Youtube className="details-youtube" videoId={launch?.links.youtube_id}/>
			</div>
		</div>
		<a onClick={ history.goBack } className="button button-back">go back</a>
	</main>
	</>
)};

export default Details;