import React from 'react';
import RellaxWrapper from 'react-rellax-wrapper';
import './features.css';

const Features = (props) => (
    <section className="features">
		<h2 className="features-title">
			{props.rocketFeatures ? props.rocketFeatures.name : `Falcon 1`} <br/>Overview
		</h2>
		<div className="overview">

			<table className="table">
				<caption className="table-title">
					Size
				</caption>
				<thead>
					<tr>
						<td className="table-column">HEIGHT</td>
						<td className="table-column">{props.rocketFeatures ? `${props.rocketFeatures.height.meters} m / ${props.rocketFeatures.height.feet} ft` : `22.25 m / 73 ft`}</td>
					</tr>
					<tr>
						<td className="table-column">DIAMETER</td>
						<td className="table-column">{props.rocketFeatures ? `${props.rocketFeatures.diameter.meters} m / ${props.rocketFeatures.diameter.feet} ft` : `1.68 m / 5.5 ft`}</td>
					</tr>
					<tr>
						<td className="table-column">MASS</td>
						<td className="table-column">{props.rocketFeatures ? `${props.rocketFeatures.mass.kg} kg / ${props.rocketFeatures.mass.lb} lb` : `30,146 kg / 66,460 lb`}</td>
					</tr>
					<tr>
						<td className="table-column">PAYLOAD TO LEO</td>
						<td className="table-column">{props.rocketFeatures ? `${props.rocketFeatures.payload_weights[0].kg} kg / ${props.rocketFeatures.payload_weights[0].lb} lb` : `450 kg / 992 lb`}</td>
					</tr>
				</thead>
			</table>
			<RellaxWrapper speed={14}>
				<img
					src={props.rocketFeatures ? `./TestReactSPA/image/${props.rocketFeatures.name.replace(' ','-').toLowerCase()}.png` : './falcon-1.png'}
					alt="rocket"
					className="rocket"
				/>
			</RellaxWrapper>
			<article>
				<h3 className="features-subtitle">DESCRIPTION</h3>
				<p className="features-text">
				{props.rocketFeatures ? `${props.rocketFeatures.description}` : `The Falcon 1 was an expendable launch system privately developed and manufactured by SpaceX during 2006-2009. On 28 September 2008, Falcon 1 became the first privately-developed liquid-fuel launch vehicle to go into orbit around the Earth.`}
				</p>
			</article>
		</div>
	</section>
);

export default Features;